nav {
  @extend %transition-all;
	@extend %clearfix;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  padding: 5px 1em;
  height: 50px;
  width: 100%;
  max-width: 1440px;
  background: transparent;
  z-index: 4;
  @media #{$brk-wide} {

  }
}


/* Logo */
.logo {
  @extend %transition-all;
	margin: 0;
  padding: 10px 0;
  float: left;
	display: inline-block;
  color: $white !important;
  text-align: center;
  z-index: 5;
	.bold {
    color: $color-2;
    font-weight: bold;
  }
  &:hover {
    color: transparentize($color-1, .2) !important;

  }
}


/* Nav Button */
#nav-btn {
  width: 50px;
	height: 50px;
	padding: 4px;
  float: right;
  display: inline-block;
  text-align: left !important;

  border-top: 2px solid $color-2;
  .closed {
    @extend %transition-all;
    cursor: pointer;
    z-index: 9999;
    padding: 2px;
    .nav-dash {
      height: 2px;
      width: 24px;
      margin: 5px auto;
      background: $white;
      position: relative;
    }
    .dash1 {
      @extend %transition-all;
      transform: rotate(0deg);
    }
    .dash2 {
      @extend %transition-all;
      transform: rotate(0deg);
    }
    .dash3 {
      @extend %transition-all;
      transform: rotate(0deg);
      display: block;
    }
  }
  .opened {
    @extend %transition-all;
    margin-left: 10px;

    .nav-dash {

    }
    .dash1 {
      @extend %transition-all;
      transform: rotate(135deg);
      right: 5px;
      top: 6px;
    }
    .dash2 {
      @extend %transition-all;
      transform: rotate(-135deg);
      top: -1px;
      right: 5px;
    }
    .dash3 {
      @extend %transition-all;
      transform: rotate(135deg);
      display: none;
    }
  }
  @media #{$brk-narrow} {
    display: none;
  }
}

#nav-list {
  @include flexbox();
  @include transition(1s);
  position: absolute;
  top: 5px;
  right: 1em;
  opacity: 0;
  background: $color-2;

  -webkit-flex-direction: column;
  flex-direction: column;

  width: 0;
  height: 0;
  overflow: hidden;
  text-align: center;
  -webkit-align-items: stretch;
  align-items: stretch;
  -webkit-justify-content: center;
  justify-content: center;
  box-shadow: 0 -2px 10px transparentize($white, .8), inset 0 0 50px transparentize($black, .8);

  .nav-item {
    @include flexbox();
    -webkit-align-items: center;
    align-items: center;
    text-align: center;

    width: 100%;
    height: 57px;
		background: transparent;
		margin-bottom: 0;
    // box-shadow: inset 0 0 30px transparentize($black, .7);
		.active {
  		color: $white !important;
			@media #{$brk-narrow} {
				color: $white !important;
				border-bottom: 2px solid $color-2;
			}
  	}
    // &:first-child {
    //   background: transparent;
    //   border: 2px solid $color-2;
    // }

    .nav-link {
      @extend %transition-all;
      color: $white;
      background: transparent;
      flex-basis: 100%;
      text-align: center;
			vertical-align: middle;
      text-transform: uppercase;
      text-decoration: none;
			font-size: 0.85rem;
      padding: 20px;
      @media #{$brk-narrow} {
        font-size: 0.85rem;
        padding: 5px 10px;
        box-shadow: none !important;
				white-space: nowrap;
      }
    }
    @media #{$brk-narrow} {
      box-shadow: none;
			background: transparent;
    }
  }
  @media #{$brk-narrow} {
    opacity: 1;
		float: right;
    box-shadow: none;
    background: none;
    flex-direction: row;
    align-items: center;
    position: static;
    width: auto;
    height: 45px;
    border: none;
  }

}
