/* Header starts here */

header {
  position: relative;
  width: auto;
  height: 100vh;
  background: linear-gradient(transparentize($black, .3), transparentize($black, .3) 100%),
              // linear-gradient(0deg, #fff, transparent),
              $black url('../data/myself.jpg') no-repeat center;
  background-size: cover;
  .container {
    height: calc(100vh - 50px);

    h1 {
      position: absolute;
      color: $color-2;
      font-family: 'Roboto', Arial, sans-serif;
      font-size: 2.5rem;
      text-shadow: 1px -1px 3px $black;
      text-align: center;
      font-weight: lighter;
      text-transform: uppercase;
      max-width: 260px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-bottom: 1px solid $white;
      @media #{$brk-max-height-320} {

        font-size: 3rem;
      }
      @media #{$brk-min-height-640} {


      }
      @media #{$brk-min-height-1024} {

      }
      @media #{$brk-tablets} {
        max-width: 320px;
        font-size: 2.5rem;
      }
      @media #{$brk-narrow} {
        max-width: 350px;
        font-size: 3.5rem;
        left: 50%;


      }
      @media #{$brk-wide} {
        max-width: 400px;
        font-size: 4rem;
        border-bottom: 1px solid $white;
      }

    }
  }
}
