.treehouse-activity {
  .heading-3 {
    color: lighten($black, 20%);
    font-family: Helvetica, sans-serif;
    font-size: 1.5rem;
    font-weight: normal;
    text-align: center;
  }
  #last-badges {
    color: $black;
    @include flexbox();
    flex-wrap: wrap;
    .badge {
      @include flexbox();
      flex-direction: column;
      align-items: flex-start;
      flex-wrap: nowrap;
      flex-grow: 1;
      margin: 0 auto 3em;
      max-width: 225px;
      background: $silver;
      box-shadow: -1px 2px 3px transparentize($black, .8);
      .badge-heading {
        margin: 0 auto;
        padding: 1rem;
        color: lighten($black, 40%);
        font-size: 1.125rem;
        font-weight: lighter;
        text-align: center;
        width: auto;
        min-height: 100px;
      }
      .badge-icon {
        width: 50%;
        margin: 0 auto 2em;
      }
      .badge-p {
        color: lighten($black, 40%);
        font-size: 0.85rem;
        margin: 0 0 0 10%;
        max-width: 90%;
        padding-bottom: 5px;
        .color-2 {
          color: $color-2;
        }
      }
      .badge-a {
        margin-top: auto;
        display: block;
        font-size: .75rem;
        text-align: center;
        color: $color-2;
        width: 100%;
        padding: 1em;
      }
      @media #{$brk-tablets} {
        width: 50%;
      }
      @media #{$brk-narrow} {
        max-width: 257px;
      }
      @media #{$brk-wide} {
        max-width: 355px;
      }
    }
  }
}


.responsive-design {
  width: 100%;
  height: auto;
}
